* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
}

.media-modal.wp-core-ui {
    color: $black;
}

p {
    margin-bottom: 10px;
}

html,
body {
    min-height: 100%;
    width: 100%;
    overflow-x: hidden;
}

html {
    font-size: 62.5%;
    background: none;
}

body {
    font-size: 1.6rem;
    line-height: 2.4rem;
    color: $white;
    height: 100%;
}

.page {
    margin: 0;
}

.video_wrapper {
    @include span-columns(12);
    padding-bottom: 56.25%;
    height: 0;
    position: relative;
    cursor: pointer;

    iframe {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }

    &.video_notation {
        padding-bottom: 124.25%;

        @include media($mobile) {
            padding-bottom: 175.25%;
        }
    }
}

.video_container {
    position: relative;

    iframe {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
}

.wrapper {
    @include span-columns(12);
    position: relative;
    background: #fff;
    z-index: 20;
    height: 100%;
    width: 100%;
    display: inline-block;
    -webkit-transition: 0.5s ease-in-out;
    -moz-transition: 0.5s ease-in-out;
    -o-transition: 0.5s ease-in-out;
    transition: 0.5s ease-in-out;
}

.full_width {
    @include span-columns(12);
    position: relative;
}

a {
    text-decoration: none;
    cursor: pointer;
}

ul {
    list-style: none;
}

img {
    width: 100%;
    height: auto;
    display: block;
}

::selection {
    background: lighten($orange, 5%);
    opacity: 0.8;
    color: $white;
    text-shadow: 0 0 1px $charcoal;
}

::moz-selection {
    background: lighten($orange, 5%);
    opacity: 0.8;
    color: $white;
    text-shadow: 0 0 1px $charcoal;
}

.oldies {
    background: lighten($charcoal, 20%);
    color: $white;
    text-align: center;
    padding: 1rem 0;
}

input[type="text"],
input[type="email"],
input[type="password"],
textarea,
select {
    @include media($mobile) {
        font-size: 16px !important;
    }
}

.button:not(.ed_button):not([type="checkbox"]) {
    @include basicButton;

    &:hover {
        span {
            opacity: 100%;
            width: 30px;
        }
    }
}

.page_content {
    padding-top: 135px;
    background: $darkBg;

    &.member {
        padding-top: 150px;
        padding-bottom: 100px;
        background: $white;

        @include media($mobile) {
            padding-bottom: 40px;
            padding-top: 180px;
        }

        .sub_header {
            h2 {
                color: $black;
            }
        }

        &.passions {
            padding-bottom: 0;
        }
    }

    &.free_lessons {
        padding-bottom: 100px;
    }
}

.mc4wp-form {
    position: relative;
    display: inline-block;
    width: 100%;
    margin: 0 auto;

    .mc4wp-form-fields {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 10px;

        @include media($tablet) {
            flex-direction: column;
            -ms-flex-direction: column;
        }
    }

    .mc4wp-response {
        position: absolute !important;
        width: 100% !important;
    }

    .email {
        width: 79%;
        display: inline-block;
        margin: 0;

        @include media($tablet) {
            width: 100%;
        }
    }

    input[type="email"] {
        height: 45px;
        margin: 0;

        @include media($tablet) {
            width: 100%;
        }
    }

    .submit {
        display: inline-block;
        margin: 0;

        @include media($tablet) {
            width: 100%;
        }
    }

    input[type="submit"] {
        @include interextrabold;
        margin: 0;

        @include media($tablet) {
            width: 100%;
        }
    }
}

.social_media_column {
    h4 {
        margin: 0 0 20px 0;
        text-align: center;
    }

    .content_box {
        display: -webkit-box;
        display: -ms-flexbox;
        display: inline-flex;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
        -ms-flex-direction: column;
        flex-direction: column;
        text-align: center;
        margin-bottom: 20px;
    }

    .columns_wrap {
        padding: 10px;
        border: 1px solid $red;
        background: $darkerGray;
        border-radius: 6px;
        -webkit-border-radius: 6px;
        -moz-border-radius: 6px;
    }

    .column {
        @include span-columns(4);

        .icon_wrap {
            padding: 10px 20%;

            img {
                max-width: 55px;
                margin: 0 auto;
            }
        }

        .icon_wrap_sponsor {
            img {
                border: 1px solid $borderGray;
                border-radius: 50%;
                -webkit-border-radius: 50%;
                -moz-border-radius: 50%;
                max-width: unset;
            }
        }

        p {
            @include span-columns(12);

            a {
                @include interregular;
                color: $grayText;
                font-size: 16px;
            }
        }
    }
}

ul.check_list {
    margin: 0;
    padding: 0;

    li {
        position: relative;
        padding-left: 30px;
        margin-bottom: 10px;

        &:before {
            content: "";
            position: absolute;
            background: url(../images/check.png) no-repeat;
            width: 20px;
            height: 15px;
            left: 0;
            top: 5px;
        }

        p {
            line-height: 1.6;
            color: $grayText;
            margin: 0;
        }
    }
}

.numbered_list {
    @include numberedList;
}
.light_gray_bg {
    background: $lightGray;
}

.box_shadow {
    @include boxShadow;
}

:root {
    --before-content: "";
}
.logged-in input[type="file"]::before {
    content: var(--before-content) !important;
    display: flex;
    justify-content: center;
    align-items: center;
}
