#fep-wrapper {
    #fep-header {
        @include boxShadow;
        @include intermedium;
        display: block;
        padding: 40px;
        border: none;
        margin-bottom: 40px;
        background: $lightGray;

        img {
            display: none;
        }

        div > div {
            color: $grayText;

            @include media($mobile) {
                flex-basis: 100%;
            }
        }

        strong {
            @include smallFont;
            color: $black;
        }

        .fep-header-content {
            color: $darkGray;
            width: 100%;
            display: flex;
            justify-content: space-between;
            align-items: flex-start;

            @include media($mobile) {
                flex-wrap: wrap;
            }
        }

        .fep-header-unread-text,
        .fep-header-box-size {
            @include interregular;
            color: $darkGray;
            text-align: right;

            @include media($tablet) {
                font-size: 1.4rem;
            }

            @include media($mobile) {
                flex-basis: 100%;
                text-align: left;
            }
        }

        .fep-header-box-size {
            @include interregular;
        }
    }
    #fep-footer {
        border: none;
    }
    #fep-menu-toggle-button {
        background-color: unset;
        border: 1px solid $borderGray;
        width: 100%;
        margin: 0 auto 20px;

        &:after {
            font-size: 20px;
            margin-top: -6px;
        }

        &.fep-menu-toggle-expanded:after {
            line-height: 1;
            font-size: 32px;
            margin-top: -10px;
        }
    }
    #fep-menu {
        @include interregular;
        padding: 0 20px;

        #fep-menu-message_box,
        #fep-menu-newmessage,
        #fep-menu-settings,
        #fep-menu-announcements,
        #fep-menu-directory,
        .fep-button {
            background: transparent;
            border: none;
            color: $darkGray;
            border-bottom: 2px solid transparent;
            border-radius: 0;
            font-size: 1.5rem;
        }

        #fep-menu-message_box:hover,
        #fep-menu-newmessage:hover,
        #fep-menu-settings:hover,
        #fep-menu-announcements:hover,
        #fep-menu-directory:hover,
        .fep-button:hover,
        #fep-menu-message_box.fep-button-active,
        #fep-menu-newmessage.fep-button-active,
        #fep-menu-settings.fep-button-active,
        #fep-menu-announcements.fep-button-active,
        #fep-menu-directory.fep-button-active,
        .fep-button-active {
            background: transparent;
            color: $yellowHover;
            border: none;
            border-bottom: 2px solid $yellowHover;
            font-size: 1.5rem;
            border-radius: 0;
        }
    }

    #fep-content {
        @include boxShadow;
        background: $lightGray;
        padding: 50px;
        border: none;
        margin-top: -1px;

        .fep-error {
            
        }

        input[type="search"] {
            width: 100%;
            -webkit-box-sizing: border-box;
            -moz-box-sizing: border-box;
            box-sizing: border-box;
            margin: 0;
        }

        > h2 {
            @include mediumFont;
            margin-bottom: 30px;
        }

        form {
            margin: 0;
        }
    }

    .fep-action-table {
        select {
            margin-top: 0;
        }

        > div > div {
            width: auto;

            @include media($mobile) {
                width: 18%;
            }

            @include media($smallMobile) {
                width: 100%;
            }
        }
    }

    .fep-table {
        margin-top: 10px;

        > div {
            display: flex;
            align-items: flex-start;
            gap: 10px;

            @include media($mobile) {
                flex-wrap: wrap;
            }

            .fep-bulk-action {
                min-width: 145px;
                display: flex;
                gap: 1rem;

                @include media($mobile) {
                    width: 100%;
                }

                @include media($smallMobile) {
                    flex-wrap: wrap;
                }

                select {
                    margin: 0;
                }

                > div {
                    @include media($smallMobile) {
                        width: 100%;
                    }
                }
            }

            .fep-filter {
                margin-left: auto;
                margin-top: 0;
                min-width: 145px;
                width: auto;

                @include media($mobile) {
                    width: 100%;
                }
            }

            .fep-button {
                margin: 0;
                background: $yellow;
                color: $black;
                height: 55px;
                border: none;

                @include media($mobile) {
                    height: 45px;
                    width: 100%;
                }

                &:hover {
                    background: $yellowHover;
                }
            }
        }
    }

    .wp-editor-container {
        border-top-right-radius: 0;
    }

    .wp-editor-tabs {
        @include media($mobile) {
            display: flex;
            width: 100%;
        }

        .wp-switch-editor {
            background: rgba(155, 155, 155, 0.1);
            padding: 15px 30px;
            border-top-right-radius: 6px;
            border-top-left-radius: 6px;
            width: 150px;
            margin-top: 5px;
            color: $darkerGray;

            @include media($mobile) {
                width: 50%;
            }
        }
    }

    .html-active .wp-switch-editor.switch-html,
    .tmce-active .wp-switch-editor.switch-tmce {
        background: $white;
    }

    .mce-top-part {
        @include textEditorToolbar;
    }

    .div.mce-toolbar-grp,
    div.mce-toolbar-grp {
        border-bottom: none;
        background: none;
    }

    .mce-top-part:before {
        content: none;
    }

    .mce-stack-layout-item {
        margin-bottom: 0;
    }

    .fep-label {
        display: none;
    }

    .fep-field {
        position: relative;
    }

    #fep-result {
        z-index: 1;
        bottom: -18px;
        background: $lightGray;
    }

    #fep_upload {
        @include span-columns(12);

        input {
            margin: 10px 0;
            @include uploadButton;
        }

        a.fep-attachment-field {
            display: inline-block;
            color: $black;
            margin-bottom: 5px;
        }
    }

    .fep-attachment-field-a {
        position: absolute;
        right: 0;
        bottom: 0;
    }

    a:not(.fep-button, .fep-button-active) {
        color: $red;
    }

    button[type="submit"] {
        @include basicButton;
        background: $yellow;

        &:hover {
            background: $yellowHover;
        }
    }

    #fep-attachment-note {
        @include span-columns(12);
    }

    .fep-success {
        background: $yellow;
    }

    .fep-message .fep-message-title-heading,
    .fep-per-message .fep-message-title {
        background-color: $lightGray;
    }

    .fep-loading-gif-div {
        @include media($smallMobile) {
            @include span-columns(4);
        }
    }

    .fep-odd-even > div:nth-child(even),
    .fep-odd-even > div:nth-child(odd) {
        background-color: unset;
        border-bottom: 1px solid $borderGray;
        padding: 30px 0;
        align-items: center;
    }

    #fep-box-content-content {
        min-height: 200px;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;

        .fep-error {
            @include interregular;
            color: $darkGray;
            background: none;
            border: none;
        }

        .fep-cb-check-uncheck-all-div {
            @include intersemibold;
            margin-right: auto;
            margin-bottom: 10px;
            color: $darkGray;

            input {
                margin-right: 5px;
            }
        }
    }

    .token-input-list-facebook {
        border: none;
        width: 100%;
        margin-top: 40px;
        background: none;

        .token-input-input-token-facebook {
            width: 100%;
            input {
                margin-bottom: 0;
            }
        }
    }

    .fep-form-field {
        margin-bottom: 15px;

        &.fep-form-field-blocked_users {
            margin-bottom: 0;
        }
        &.fep-form-field-announcement_roles {
            .fep-field {
                display: grid;
                grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
                row-gap: 10px;

                @include media($mobile) {
                    grid-template-columns: repeat(3, 1fr);
                }

                @include media($smallMobile) {
                    grid-template-columns: repeat(2, 1fr);
                }
            }
        }

        label {
            @include interregular;
            display: flex;
            align-items: center;
            gap: 10px;

            input {
                width: 15px;
                height: 15px;
            }
        }
    }

    .fep-column.fep-column-fep-cb {
        width: auto;
        margin: auto 0;
    }

    .fep-column.fep-column-avatar {
        width: auto;
    }

    .fep-column-fep-cb {
        padding-right: 0;
    }

    .fep-avatar-p-90 {
        min-width: unset;
        display: flex;
        justify-content: flex-start;
        align-items: center;

        &.fep-avatar-p .fep-avatar-1,
        &.fep-avatar-p .fep-avatar-2,
        .fep-avatar-more-60,
        .fep-avatar-group-60 {
            height: auto;
            width: auto;
        }

        &.fep-avatar-p .fep-avatar-2 {
            margin-left: -5px;
            position: relative;
            left: auto;
            top: auto;
        }
    }

    .fep-column.fep-column-avatar img {
        width: 50px;
        height: auto;
        background: $white;
    }

    .fep-column.fep-column-title {
        margin-left: auto;
        width: 20%;

        a {
            @include intersemibold;
            color: $darkRed;

            &:hover {
                color: $red;
            }
        }

        .fep-table .fep-message-excerpt {
            color: $darkGray;
        }
    }

    .fep-column.fep-column-name {
        width: 49%;
        vertical-align: middle;

        @include media($smallMobile) {
            width: 41%;
            font-size: 15px;
        }
    }

    .fep-column.fep-column-block_unblock {
        display: none;
    }

    .fep-column.fep-column-send_message {
        text-align: right;
        margin-left: auto;

        a {
            background: $yellow;
            color: $black !important;
            padding: 6px 10px;
            display: inline-block;
            font-size: 16px;
            text-decoration: none;
            -webkit-border-radius: 4px;
            -moz-border-radius: 4px;
            border-radius: 4px;

            &:hover {
                background: $yellowHover;
            }

            @include media($mobile) {
                font-size: 14px;
                padding: 4px 10px;
            }
        }
    }

    .fep-column.fep-column-block_unblock a,
    .fep-column.fep-column-send_message a {
        color: $black;
    }

    .fep-pagination a {
        color: $red;
    }

    .fep-pagination li.active a {
        background-color: $lightGray;
        border-color: $red;
    }

    .fep-avatar-p .fep-avatar-1 img,
    .fep-avatar-p .fep-avatar-2 img {
        width: 30px;
    }
}
