.forum_wrapper {
    color: $black;
    @include interregular;

    .mejs-overlay {
        display: none;
    }

    .entry-header {
        display: none;
    }

    .forums_home {
        background: $white;
        z-index: 1;

        a {
            padding-left: 0 !important;
            color: $grayText;
            padding-top: 8px;
            padding-bottom: 5px;
            display: block;
            float: left;
        }
    }

    .entry-content {
        margin: 0;
    }

    h1 {
        font-size: 22px;
    }

    img {
        width: auto;
    }

    .bbp-topic-revision-log {
        display: none;
    }

    .forum_content {
        @include span-columns(9);

        @include media($mobile) {
            @include span-columns(4);
            padding-bottom: 0;
        }
    }

    .entry-title {
        font-size: 30px;
        display: inline-block;

        @include media($tablet) {
            font-size: 25px;
        }

        @include media($mobile) {
            font-size: 20px;
        }
    }

    .edit_link {
        display: inline-block;
        color: $black;
        margin-left: 5px;
        font-size: 14px;
    }

    .bbp-topic-form,
    .bbp-reply-form,
    .bbp-topic-tag-form {
        width: 100%;
        padding: 20px;
    }

    #new-post {
        @include boxShadow;
        padding: 20px;

        .bbp-form {
            border: none !important;
            padding: 0 !important;

            @include media($smallMobile) {
                margin-bottom: 0;
            }
        }

        /*  .bbp-the-content-wrapper {
            @include span-columns(12);
        }

        p.adjust {
            @include span-columns(12);
        }

        h4 {
            @include span-columns(12);

            &.rec_text {
                display: none;
            }
        } */
    }

    #rtmedia_uploader_filelist {
        @include span-columns(12);
    }

    .bbp-breadcrumb {
        p {
            margin-bottom: 0 !important;
        }

        a {
            @include intermedium;
            color: $darkGray;
            font-size: 13px;

            @include media($mobile) {
                font-size: 12px;
            }

            &.bbp-breadcrumb-home {
                display: none;

                + .bbp-breadcrumb-sep {
                    display: none;
                }
            }
        }

        .bbp-breadcrumb-sep {
            color: $darkGray;
            font-size: 14px;
        }

        .bbp-breadcrumb-current {
            color: $red;
            font-size: 13px;
        }
    }

    #bbpress-forums {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-between;
        overflow-x: scroll;

        fieldset.bbp-form label {
            font-size: 1.3rem;
        }

        div.bbp-the-content-wrapper {
            width: 100%;
            display: inline-block;
        }

        .bbp-reply-header {
            padding: 15px 10px;
            margin-bottom: 20px;
            border-bottom: 1px solid;
            border-color: rgba(155, 155, 155, 0.2);
            background-color: unset;
            border-top: none;

            a {
                font-size: 12px;
                color: $darkGray;
            }
        }

        #subscription-toggle {
            @include interregular;
            margin-left: auto;
            margin-right: 20px;
            color: $darkGray;

            @include media($mobile) {
                width: 100%;
                text-align: left;
                margin: 20px 0;
            }

            a {
                color: $yellow;
                font-size: 16px;

                @include media($mobile) {
                    font-size: 14px;
                }

                &:hover {
                    color: $yellowHover;
                }
            }
        }

        .bbp-row-actions {
            #subscription-toggle {
                padding: 0 5px 2px;
                float: none;

                span.is-subscribed {
                    a {
                        background-color: transparent;
                        border: none;
                        font-size: 20px;
                        color: $black;
                    }
                }
            }

            #favorite-toggle {
                a {
                    font-size: 20px;
                    background: transparent;
                    color: $black;
                    border: none;
                    padding: 0 5px 2px;
                    border-radius: 4px;
                    -webkit-border-radius: 4px;
                    -moz-border-radius: 4px;
                }
            }
        }

        ul.bbp-forums {
            position: relative;
            border: none;
            margin-top: 20px;
            min-width: 500px;
            width: 100%;
        }

        div.bbp-search-form {
            display: none;
        }

        li.bbp-header {
            line-height: 1;
            background: $darkGray;
            color: $white;
            padding: 15px;
            @include intermedium;
            font-size: 16px;
            border-radius: 6px;
            -webkit-border-radius: 6px;
            -moz-border-radius: 6px;
            border-top: none;

            @include media($tablet) {
                font-size: 14px;
                padding: 10px 0 10px 10px;
            }

            @include media($mobile) {
                font-size: 12px;
            }

            li.bbp-forum-info {
                width: 30%;
                text-transform: none;
                text-align: left;
            }

            li.bbp-forum-freshness {
                position: relative;
                width: 20%;
                text-align: center;

                @include media($tablet) {
                    text-align: center;
                    font-size: 16px;
                }

                @include media($mobile) {
                    font-size: 12px;
                }
            }

            li.bbp-forum-topic-count {
                width: 25%;
                text-align: left;
            }

            li.bbp-forum-reply-count {
                width: 25%;
                text-align: left;
            }

            li.bbp-topic-title {
                width: 30%;
                //width: auto;
                display: flex;
                flex-direction: column;

                a {
                    font-size: 16px;
                    color: $black;

                    @include media($tablet) {
                        font-size: 14px;
                    }

                    @include media($mobile) {
                        font-size: 12px;
                    }
                }

                p.bbp-topic-meta {
                    display: none;
                }
            }

            li.bbp-topic-voice-count {
                width: 25%;
            }

            li.bbp-topic-reply-count {
                width: 25%;
            }

            li.bbp-topic-freshness {
                width: 20%;

                @include media($tablet) {
                    text-align: center;
                }
            }

            #subscription-toggle {
                padding: 0;
                margin: 0;
                background: transparent;
                font-size: 16px;
                padding-right: 15px;

                @include media($tablet) {
                    font-size: 14px;
                }

                @include media($mobile) {
                    font-size: 11px;
                }

                @include media($smallMobile) {
                    width: auto;
                }

                a {
                    @include media($tablet) {
                        font-size: 14px;
                    }

                    @include media($mobile) {
                        font-size: 11px;
                    }

                    &:hover {
                        background: transparent;
                    }
                }
            }

            #favorite-toggle {
                @include media($mobile) {
                    padding-right: 0;
                    font-size: 11px;
                }

                a {
                    color: $lightYellow;
                }
            }
        }

        li.bbp-body {
            ul.forum {
                position: relative;
                font-size: 16px;
                border-top: none;
                padding: 20px;
                margin: 10px 0;
                border-radius: 10px;
                box-shadow: inset -1px 0px 50px -8px rgba(155, 155, 155, 0.1);

                @include media($tablet) {
                    font-size: 14px;
                }

                @include media($mobile) {
                    font-size: 12px;
                }
            }

            ul.topic {
                position: relative;
                padding-left: 0;
                padding-right: 0;
                border: none !important;
                display: flex;
                justify-content: space-between;
                padding: 20px;
                margin: 10px 0;
                border-radius: 10px;
                box-shadow: inset -1px 0px 50px -8px rgba(155, 155, 155, 0.1);
            }

            .type-topic,
            .type-reply {
                padding: 20px;
                margin: 10px 0;
                border-radius: 10px;
                box-shadow: inset -1px 0px 50px -8px rgba(155, 155, 155, 0.1);
            }

            li.bbp-forum-info {
                @include interregular;
                width: 30%;
                text-transform: none;

                .title_wrap {
                    display: flex;
                    flex-direction: column;

                    a {
                        float: left;

                        @include media($tablet) {
                            width: 100%;
                        }
                    }

                    p {
                        @include interbold;
                        width: auto;
                        display: inline-block;
                        color: $red;
                        font-size: 12px;

                        @include media($tablet) {
                            font-size: 11px;
                        }

                        @include media($mobile) {
                            font-size: 10px;
                            line-height: 1.8;
                            margin-top: 5px;
                        }
                    }
                }

                .bbp-forum-content {
                    float: left;
                }

                .bbp-forum-title {
                    @include interbold;

                    @include media($mobile) {
                        line-height: 1;
                    }
                }

                a {
                    color: $black;
                    font-size: 16px;

                    @include media($tablet) {
                        font-size: 14px;
                    }

                    @include media($mobile) {
                        font-size: 12px;
                    }
                }

                li.bbp-forum-content {
                    font-size: 14px;
                    color: $darkGray;

                    @include media($tablet) {
                        font-size: 12px;
                    }

                    @include media($mobile) {
                        line-height: 1;
                    }
                }
            }

            li.bbp-forum-topic-count,
            li.bbp-forum-reply-count {
                width: 25%;
                text-align: left;

                @include media($tablet) {
                    font-size: 14px;
                }

                @include media($mobile) {
                    font-size: 12px;
                }
            }

            li.bbp-forum-freshness {
                position: relative;
                width: 20%;

                .bbp-topic-freshness-author {
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;

                    .bbp-author-avatar {
                        height: 75px;
                        width: 75px !important;
                        border: 1px solid $borderGray;
                        overflow: hidden;
                        margin: 0 auto;

                        @include media($mobile) {
                            height: 50px;
                            width: 50px !important;
                        }
                    }

                    img {
                        border-radius: 100%;
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                    }
                }

                .bbp-author-name {
                    width: 100%;
                    float: left;
                    text-align: center;
                    padding-top: 10px;
                }

                a {
                    @include interregular;
                    position: relative;
                    float: left;
                    width: 60%;
                    text-align: left;
                    color: $black;
                    font-size: 12px;

                    @include media($tablet) {
                        width: 100%;
                        text-align: center;
                        font-size: 10px;
                    }

                    &:first-child {
                        width: 40%;

                        @include media($tablet) {
                            width: 100%;
                            text-align: center;
                        }
                    }

                    &:last-child {
                        @include intermedium;
                        font-size: 14px;
                        color: $red;
                        width: 100%;
                        text-align: center;

                        @include media($tablet) {
                            padding-top: 0;
                            font-size: 12px;
                        }
                    }

                    img {
                        width: 100%;
                        border: none;
                        border-radius: 4px;
                        -webkit-border-radius: 4px;
                        -moz-border-radius: 4px;

                        @include media($tablet) {
                            width: 40%;
                            margin: 0 auto;
                        }
                    }
                }
            }

            li.bbp-topic-title {
                width: 30%;
                display: flex;
                flex-direction: column;

                a {
                    font-size: 16px;
                    color: $black;

                    &.float {
                        float: left;
                    }

                    @include media($tablet) {
                        font-size: 14px;
                        width: 100%;
                    }

                    @include media($mobile) {
                        font-size: 12px;
                    }
                }

                p {
                    @include interbold;
                    width: auto;
                    display: inline-block;
                    color: $red;
                    font-size: 12px;
                }

                p.bbp-topic-meta {
                    display: none;
                }
            }

            li.bbp-topic-voice-count,
            li.bbp-topic-reply-count {
                width: 25%;
            }

            li.bbp-topic-freshness {
                position: relative;
                width: 20%;

                .bbp-topic-freshness-author {
                    width: 100%;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;

                    @include media($tablet) {
                        @include span-columns(8);
                    }

                    .bbp-author-avatar {
                        height: 75px;
                        width: 75px !important;
                        border: 1px solid $borderGray;
                        overflow: hidden;
                        margin: 0 auto;

                        @include media($mobile) {
                            height: 50px;
                            width: 50px !important;
                        }
                    }

                    img {
                        border-radius: 100%;
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                    }
                }

                a {
                    @include interregular;
                    position: relative;
                    display: inline-block;
                    text-align: left;
                    color: $black;
                    font-size: 12px;

                    @include media($tablet) {
                        padding-left: 0;
                    }

                    &:first-child {
                        width: 40%;
                        padding-left: 0;
                        float: left;

                        @include media($tablet) {
                            @include span-columns(8);
                            text-align: center;
                        }
                    }

                    &:last-child {
                        @include intermedium;
                        font-size: 14px;
                        color: $red;

                        @include media($tablet) {
                            @include span-columns(8);
                            text-align: center;
                            padding-top: 0;
                        }

                        @include media($mobile) {
                            font-size: 12px;
                        }
                    }

                    &:last-child {
                        @include media($tablet) {
                            @include span-columns(8);
                            text-align: center;
                        }

                        @include media($mobile) {
                            font-size: 10px;
                            line-height: 1.2;
                        }
                    }

                    img {
                        width: 100%;
                        border: none;
                        border-radius: 4px;
                        -webkit-border-radius: 4px;
                        -moz-border-radius: 4px;
                        margin: 0 auto;

                        /* @include media($tablet) {
                            width: 40% !important;
                        }

                        @include media($mobile) {
                            width: 60% !important;
                        } */
                    }
                }
            }
        }

        li.bbp-footer {
            display: none;
        }

        .topic-author {
            padding-left: 0;
            padding: 0 20px 20px;
            margin: 10px 0;
            border-radius: 10px;
            box-shadow: inset -1px 0px 50px -8px rgba(155, 155, 155, 0.1);
        }

        p.bbp-topic-meta {
            @include span-columns(12);

            @include media($tablet) {
                text-align: center;
                width: 100%;
            }

            span {
                white-space: normal;
                width: 100%;
                display: block;
                text-align: center;
                border-radius: 100%;
            }
        }

        div.bbp-template-notice.info {
            display: none;
        }

        .bbp-forum-description,
        .bbp-topic-description {
            padding: 5% 0 8% 20%;
            font-size: 15px;
            line-height: 1.1;

            @include media($tablet) {
                font-size: 1.5vw;
                padding-left: 18%;
            }

            @include media($mobile) {
                padding: 3% 0 8% 19%;
                font-size: 2vw;
                line-height: 1.2;
            }

            @include media($smallMobile) {
                padding: 3% 0 6% 24%;
                font-size: 12px;
            }

            a {
                @include intermedium;
                position: relative;

                &:first-child {
                    position: absolute;
                    left: 5px;
                    top: -5px;
                    width: 16%;

                    @include media($mobile) {
                        width: 15%;
                    }

                    @include media($smallMobile) {
                        width: 20%;
                        top: -6%;
                    }

                    img {
                        width: 100%;
                        border: none;
                        border-radius: 4px;
                        -webkit-border-radius: 4px;
                        -moz-border-radius: 4px;
                    }
                }

                &:nth-child(2) {
                    color: $red !important;
                }

                &:last-child {
                    width: 100%;
                    float: left;
                    color: $grayText;
                    font-size: 13px;
                    margin-top: 5px;

                    @include media($tablet) {
                        font-size: 11px;
                    }
                }
            }
        }

        .forum-titles {
            font-size: 1.8rem;
            display: flex;
            justify-content: space-between;

            @include media($mobile) {
                font-size: 1.4rem;
            }

            .bbp-topic-freshness {
                text-align: center;
            }
        }

        .bbp-topics {
            border: none;
            margin-top: 20px;

            .bbp-body {
                ul {
                    border-bottom: 1px solid $borderGray;
                }
            }
        }

        .bbp-pagination-count {
            padding: 3px 5px;
            font-size: 14px;

            @include media($mobile) {
                font-size: 10px;
                padding: 0 5px;
            }
        }

        li.bbp-header .bbp-reply-author {
            width: 50%;
            text-align: left;

            @include media($mobile) {
                width: 33%;
                font-size: 12px;
            }
        }

        .bbp-replies {
            border: none;
            width: 100%;

            hr {
                width: 100%;
            }

            li.bbp-header {
                border-bottom-right-radius: 0;
                border-bottom-left-radius: 0;
                -webkit-border-bottom-left-radius: 0;
                -webkit-border-bottom-right-radius: 0;
                -moz-border-radius-bottomleft: 0;
                -moz-border-radius-bottomright: 0;

                .bbp-reply-content {
                    margin-left: 0;

                    @include media($mobile) {
                        width: 100%;
                    }
                }
            }
        }

        .bbp-body {
            div.bbp-reply-author {
                position: relative;
                float: left;
                width: 50%;
                text-align: left;
                display: flex;
                flex-direction: column;

                @include media($mobile) {
                    font-size: 12px;
                    width: 37%;
                    margin: 0;
                    padding-left: 0;
                    padding-right: 5px;
                }

                a {
                    display: flex;
                    flex-direction: column;
                }

                > p {
                    position: absolute;
                    width: auto;
                    top: 100%;
                    left: 0;
                    margin-top: 20px;

                    @include media($mobile) {
                        margin-top: 5px;
                    }

                    a {
                        background: $red;
                        color: $white;
                        padding: 7px 12px;
                        border-radius: 4px;
                        -webkit-border-radius: 4px;
                        -moz-border-radius: 4px;
                        font-size: 16px;

                        @include media($tablet) {
                            padding: 6px 12px;
                            font-size: 14px;
                        }

                        @include media($mobile) {
                            font-size: 12px;
                            padding: 4px 6px;
                        }

                        &:hover {
                            background: #c30b21;

                            @include media($mobile) {
                                //background: transparent;
                                //color: #c30b21;
                            }
                        }
                    }
                }

                .bbp-author-avatar {
                    float: left;

                    @include media($tablet) {
                        width: 100%;
                    }

                    img {
                        margin: 0;
                        width: 100%;
                        float: left;
                        border: none;
                        border-radius: 4px;
                        -webkit-border-radius: 4px;
                        -moz-border-radius: 4px;

                        @include media($mobile) {
                            float: none;
                            position: relative;
                            top: 0;
                        }
                    }
                }

                .bbp-author-name {
                    float: left;
                    clear: none;
                    font-size: 16px;
                    color: $red;

                    @include media($tablet) {
                        width: 100%;
                        margin: 0;
                        font-size: 14px;
                    }

                    @include media($mobile) {
                        margin: 0;
                    }
                }

                .bbp-author-role {
                    float: left;
                    font-size: 12px;
                    background: #ddb72e;
                    padding: 2px 8px 4px;
                    line-height: 1.2;
                    border-radius: 4px;
                    -webkit-border-radius: 4px;
                    -moz-border-radius: 4px;
                    width: 8rem;
                    text-align: center;

                    @include media($tablet) {
                        font-size: 11px;
                    }

                    @include media($mobile) {
                        font-size: 10px;
                        margin-top: 0;
                    }
                }

                .bbp-reply-ip {
                    display: none;
                }

                .bbp-reply-post-date {
                    float: left;
                    color: $grayText;

                    @include media($tablet) {
                        font-size: 11px;
                        width: 100%;
                    }

                    @include media($mobile) {
                        font-size: 9px;
                        margin-left: 0;
                        line-height: 1.6;
                        margin-top: 5px;
                    }
                }
            }

            div.bbp-reply-content {
                width: 50%;
                display: inline-block;
                margin-left: 0 !important;
                padding: 0;

                @include media($mobile) {
                    width: 63%;
                    margin: 0;
                    float: none;
                }

                img {
                    margin: 0 auto;
                }

                p {
                    color: $grayText;
                    font-size: 14px;
                }
            }

            .topic {
                .bbp-reply-content {
                    margin-left: 0;
                    width: 50%;
                    float: left;
                    text-align: left;

                    @include media($mobile) {
                        float: none;
                        width: 63%;
                    }
                }
            }
        }

        .bbp-topics {
            float: left;
            width: 100%;
        }

        #bbp-user-wrapper {
            //margin-top: 36px;

            @include media($mobile) {
                //margin-top: 20px;
            }

            .topic_button {
                display: none;
            }

            h2.entry-title {
                @include span-columns(12);

                @include media($tablet) {
                    font-size: 16px;
                }
            }

            #bbp-single-user-details {
                @include span-columns(12);

                #bbp-user-profile {
                    @include span-columns(12);
                    margin-bottom: 15px;

                    @include media($mobile) {
                        padding-left: 10px;
                    }

                    h2 {
                        float: left;
                    }
                }

                #bbp-user-navigation {
                    @include span-columns(12);
                    margin-bottom: 30px;

                    @include media($mobile) {
                        border: 1px solid $borderGray;
                        border-radius: 4px;
                        -webkit-border-radius: 4px;
                        -moz-border-radius: 4px;
                    }

                    .user_mobile_nav {
                        display: none;
                        cursor: pointer;
                        z-index: 1;

                        &.open {
                            border-bottom: 1px solid $white;
                        }

                        @include media($mobile) {
                            @include span-columns(4);
                        }

                        p {
                            font-size: 16px;
                            background: $black;
                            padding: 6px 10px;
                            color: $white;
                            margin-bottom: 0;

                            span {
                                float: right;
                                font-size: 20px;

                                &.open {
                                    font-size: 40px;
                                    top: -2px;
                                }
                            }
                        }
                    }

                    .nav_wrap {
                        @include span-columns(12);

                        &.current_user {
                            li {
                                width: 16.66%;

                                @include media($mobile) {
                                    @include span-columns(4);
                                }
                            }
                        }
                    }

                    ul {
                        @include span-columns(12);
                        border: 1px solid $borderGray;
                        -webkit-transition: 0.5s ease-in-out;
                        -moz-transition: 0.5s ease-in-out;
                        -o-transition: 0.5s ease-in-out;
                        transition: 0.5s ease-in-out;

                        @include media($mobile) {
                            border: none;
                            margin-top: -222px;
                        }

                        &.open {
                            margin-top: 0;
                        }

                        li {
                            width: 20%;
                            float: left;
                            border-right: 1px solid $borderGray;
                            text-align: center;

                            @include media($mobile) {
                                @include span-columns(4);
                                border-right: none;
                                border-bottom: 1px solid $borderGray;
                            }

                            &:last-child {
                                border-right: none;

                                @include media($mobile) {
                                    border-bottom: none;
                                }
                            }

                            a {
                                color: $black;
                                font-size: 14px;

                                &:hover {
                                    color: $white;
                                    background: $black;
                                }

                                @include media($tablet) {
                                    font-size: 1.6vw;
                                }

                                @include media($mobile) {
                                    font-size: 14px;
                                }
                            }

                            &.current {
                                background: $black;

                                a {
                                    background: transparent;
                                    color: $white;
                                }
                            }
                        }
                    }
                }

                #bbp-user-avatar {
                    width: 19%;
                    float: left;
                    padding-bottom: 20px;

                    img.avatar {
                        width: 100%;
                        height: auto;
                    }
                }
            }

            #bbp-user-body,
            .profile_content {
                float: left;
                margin-left: 0;
                width: 100%;
                clear: none !important;
                padding-left: 20px;

                &.profile_body {
                    width: 81%;

                    @include media($mobile) {
                        line-height: 1;
                    }

                    > p {
                        padding-left: 20px;
                        float: left;
                        margin-top: 10px;

                        @include media($mobile) {
                            padding-left: 10px;
                        }

                        a {
                            @include interregular;
                            font-size: 16px;
                            padding: 7px 15px;
                            border-radius: 4px;
                            -webkit-border-radius: 4px;
                            -moz-border-radius: 4px;
                            background: $red;
                            color: $white;

                            &:hover {
                                background: #c30b21;
                            }

                            @include media($tablet) {
                                padding: 6px 12px;
                                font-size: 14px;
                            }

                            @include media($mobile) {
                                padding: 7px 10px;
                                font-size: 12px;
                            }
                        }
                    }

                    h2 {
                        font-size: 14px;
                    }

                    p {
                        font-size: 10px;
                        margin-right: 10px;
                    }

                    &.mailbox {
                        width: 100%;

                        .bbp-user-profile {
                            display: none !important;
                        }
                    }
                }

                .bbp-user-section {
                    @include span-columns(12);
                    line-height: 1.4;

                    .bbp-pagination:first-of-type {
                        display: none;
                    }

                    p {
                        float: left;
                        margin-right: 20px;
                        font-size: 14px;
                        color: $grayText;

                        @include media($tablet) {
                            font-size: 12px;
                        }

                        &:last-child {
                            margin-right: 0;
                        }
                    }
                }
            }
        }
    }

    .bbp-submit-wrapper {
        @include media($smallMobile) {
            margin-top: 0;
        }

        button {
            @include basicButton;
            background: $yellow;

            &:hover {
                background: $yellowHover;
            }
        }

        .button:not(.ed_button):not([type="checkbox"]) {
            @include media($smallMobile) {
                width: 100%;
            }
        }
    }

    .topic_button {
        line-height: 1;
        width: 15rem;
        margin-right: 10px;
        font-size: 14px;

        &:hover {
            background: $black;
            color: $white;
            border-color: $white;
        }

        @include media($smallMobile) {
            font-size: 12px;
            padding: 6px;
            margin-bottom: 10px;
            float: left;
        }
    }
}

.navigation-paging {
    @include span-columns(12);

    h1 {
        display: none;
    }
}

.bbp-topic-form {
    input[type="text"] {
        width: 100%;
        float: left;
    }
}

.bbp-reply-form {
    width: 100%;

    input[type="text"] {
        width: 100%;
        float: left;
    }
}

.bbp-submit-wrapper {
    width: 100%;
    //margin-top: 0 !important;

    @include media($mobile) {
        //margin-top: 20px;
    }

    button {
        float: right;
    }
}

.rec_text {
    @include span-columns(12);
    @include interitalic;
    text-align: left;
    font-size: 12px;
    margin: 0;
    line-height: 1.4;
}

#bbp_topic_subscription {
    @include media($mobile) {
        height: 13px;
    }
}

label[for="bbp_topic_subscription"] {
    @include media($mobile) {
        font-size: 12px;
    }
}

.wp-editor-container {
    border: 1px solid #c8c8c8 !important;
    border-radius: 6px;

    .quicktags-toolbar {
        @include textEditorToolbar;
        overflow-x: scroll;
    }

    textarea {
        border: none !important;
    }
}

.options_wrap {
    display: flex;
    justify-content: space-between;

    @include media($mobile) {
        flex-wrap: wrap;
    }

    p {
        width: 33%;

        @include media($mobile) {
            width: 100%;
        }
    }
}

.rtm-bbp-container {
    @include span-columns(12);
    margin: 0 !important;

    .rtm-bbp-thumb-view {
        width: 100% !important;

        a {
            display: block !important;
        }
    }
}

.rtm-file-input-container {
    width: 100%;
    float: left;

    input[type="file"] {
        width: 100%;
        float: left;
    }
}

.rtm-tabs {
    border-bottom: 1px solid $borderGray !important;

    li {
        color: $black;
        border: 1px solid $borderGray !important;
        border-bottom: none !important;
        border-top-left-radius: 4px;
        border-top-right-radius: 4px;
        -webkit-border-top-left-radius: 4px;
        -webkit-border-top-right-radius: 4px;
        -moz-border-radius-topleft: 4px;
        -moz-border-radius-topright: 4px;

        &:first-child {
            margin-left: 0;
        }

        a {
            @include interregular;
            color: $black;

            @include media($mobile) {
                font-size: 14px;
            }

            .dashicons {
                font-size: 14px;
                line-height: 24px;
            }
        }

        &.active {
            background: $black;

            a {
                color: $white;
            }
        }
    }
}

.rtm-tabs-content {
    @include span-columns(12);
    background: $white;
    text-align: center;
    border-radius: 4px;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    -webkit-box-shadow: -2px 2px 3px 1px rgba(0, 0, 0, 0.14);
    -moz-box-shadow: -2px 2px 3px 1px rgba(0, 0, 0, 0.14);
    box-shadow: -2px 2px 3px 1px rgba(0, 0, 0, 0.14);
    border: 1px solid $borderGray;

    .content {
        padding: 40px;
        margin: 20px 0 40px;
        float: none;
        display: inline-block;
        width: auto;
        border-radius: 4px;
        -webkit-border-radius: 4px;
        -moz-border-radius: 4px;

        @include media($mobile) {
            padding: 20px;
            margin: 0;
        }

        &.hide {
            display: none;
        }

        h3 {
            font-size: 24px;
            margin-top: 0;

            @include media($mobile) {
                font-size: 20px;
            }
        }

        label {
            width: 100%;
            float: left;
            text-align: left;

            @include media($mobile) {
                font-size: 14px;
            }
        }

        textarea {
            width: 100%;
            float: left;
            border: 1px solid $borderGray;
            padding: 10px;
            height: 200px;

            @include media($mobile) {
                font-size: 14px;
            }
        }

        .rtmedia-edit-title {
            @include span-columns(12);
            margin-bottom: 20px;

            input {
                height: 35px;
                width: 100%;
                float: left;
                border: 1px solid $borderGray;
                border-radius: 4px;
                -webkit-border-radius: 4px;
                -moz-border-radius: 4px;
                padding: 0 10px;
            }
        }

        .rtmedia-editor-description {
            @include span-columns(12);
        }
    }
}

.rtmedia-editor-buttons {
    @include span-columns(12);
    margin-top: 40px;

    .button {
        border: none;
        padding: 8px 40px;

        &.rtm-button-save {
            color: $white;
        }
    }
}

.d4p-attachment-addfile {
    display: none;
}

.bbp-attachments-form {
    @include span-columns(12);

    input {
        @include uploadButton;
    }
}

.mejs-inner {
    height: 100%;
}

.mejs-layers {
    height: 100%;
}

#media_upload_wait {
    float: left;
    display: none;
    color: #fff;
    text-align: center;
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    min-height: 100%;
    background: rgba(0, 0, 0, 0.8);
    z-index: 999;

    &.show {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-orient: horizontal;
        -ms-flex-direction: row;
        flex-direction: row;
        -ms-flex-wrap: wrap;
        flex-wrap: wrap;
        -webkit-box-align: center;
        -ms-flex-align: center;
        align-items: center;
    }

    .text_wrap {
        width: 100%;
        -webkit-box-orient: vertical;
        -ms-flex-direction: column;
        flex-direction: column;
        -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-direction: normal;
    }

    h2 {
        font-size: 38px;
        text-transform: uppercase;
        width: 100%;
        display: inline-block;

        @include media($tablet) {
            font-size: 30px;
        }

        @include media($mobile) {
            font-size: 22px;
        }

        @include media($smallMobile) {
            font-size: 20px;
        }
    }

    h3 {
        font-size: 28px;
        width: 100%;
        display: inline-block;

        @include media($tablet) {
            font-size: 20px;
        }

        @include media($mobile) {
            font-size: 13px;
        }

        @include media($smallMobile) {
            font-size: 11px;
        }
    }
}

.fancybox-opened {
    .fancybox-skin {
        background: transparent;
        -webkit-box-shadow: none !important;
        box-shadow: none !important;
        -moz-box-shadow: none !important;
    }
}

#link-selector {
    color: $black;
}

.subscribe_all {
    input {
        @include interextrabold;
        border: none;
        margin: 0;

        &:hover {
            background: #e1bf44;
        }
    }

    input[type="submit"] {
        margin-top: 0;
    }
}

#bbpress-forums div.odd,
#bbpress-forums ul.odd {
    background-color: unset;
}
