#global_header {
    @include span-columns(12);
    -webkit-transition: 0.5s ease-in-out;
    -moz-transition: 0.5s ease-in-out;
    -o-transition: 0.5s ease-in-out;
    transition: 0.5s ease-in-out;
    position: fixed;
    z-index: 999;

    &.external {
        .header_bottom {
            padding: 20px 0;
        }
    }

    .header_top {
        @include span-columns(12);
        -webkit-transition: 0.5s ease-in-out;
        -moz-transition: 0.5s ease-in-out;
        -o-transition: 0.5s ease-in-out;
        transition: 0.5s ease-in-out;
        border-bottom: 1px solid $darkGray;

        @include media($tablet) {
            padding: 10px 0 20px;
        }

        @include media($mobile) {
            padding: 5px 0 15px;
        }

        &.scroll {
            padding: 12px 0 2px;

            @include media($tablet) {
                padding: 5px 0 10px;
            }
        }

        &.member {
            padding: 5px 0;
            text-align: right;
            background: rgba(0, 0, 0, 0.9);

            @include media($tablet) {
                text-align: center;
            }

            a {
                color: $grayText;
                text-transform: uppercase;
                padding: 0;

                &:hover {
                    color: $white;
                }

                &:first-child {
                    margin-right: 20px;
                }

                @include media($mobile) {
                    font-size: 14px;
                }
            }
        }
    }

    .header_bottom {
        @include span-columns(12);
        background: transparent;
        z-index: 1;
        -webkit-transition: all 0.5s ease-in-out;
        -moz-transition: all 0.5s ease-in-out;
        -o-transition: all 0.5s ease-in-out;
        transition: all 0.5s ease-in-out;

        &.home_background {
            background: rgba(0, 0, 0, 0.9);
            padding: 0;
        }

        .content_wrap {
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            -webkit-box-orient: horizontal;
            -webkit-box-direction: normal;
            -ms-flex-direction: row;
            flex-direction: row;
            -ms-flex-wrap: nowrap;
            flex-wrap: nowrap;
            justify-content: flex-start;
            -webkit-justify-content: flex-start;
            align-items: center;
            -webkit-align-items: center;
            gap: 80px;

            @include media($desktop) {
                gap: 20px;
            }
        }

        .logo_wrap {
            @include media($tablet) {
                width: 100%;
                text-align: center;
            }

            @include media($mobile) {
                width: auto;
            }

            a {
                display: inline-block;
            }
        }

        .logo {
            float: left;
            max-width: 275px;
            -webkit-transition: 0.5s ease-in-out;
            -moz-transition: 0.5s ease-in-out;
            -o-transition: 0.5s ease-in-out;
            transition: 0.5s ease-in-out;

            @include media($tablet) {
                max-width: 200px;
            }

            &.scroll {
                max-width: 170px;
                padding: 0;

                @include media($mobile) {
                    margin: 10px 0;
                }
            }
        }

        .menu {
            -webkit-transition: 0.5s ease-in-out;
            -moz-transition: 0.5s ease-in-out;
            -o-transition: 0.5s ease-in-out;
            transition: 0.5s ease-in-out;
            width: 100%;

            @include media($tablet) {
                height: 100%;
                position: fixed;
                left: -100%;
                background: rgba(0, 0, 0, 0.95);
                border-top: 1px solid;
                border-color: rgba(255, 255, 255, 0.2);
            }

            nav {
                display: flex;
                justify-content: space-between;

                @include media($tablet) {
                    flex-direction: column;
                    justify-content: flex-start !important;
                    height: 100%;
                    overflow-y: scroll;
                }
            }

            ul {
                margin: 0;
                display: flex;
                align-items: center;
                padding-left: 0;

                @include media($tablet) {
                    flex-direction: column;
                }

                li {
                    padding: 15px 20px 15px 15px;

                    @include media($desktop) {
                        padding: 15px 10px;
                    }

                    @include media($tablet) {
                        padding: 0;
                    }

                    @include media($tablet) {
                        width: 100%;
                    }

                    a:not(.button) {
                        @include interregular;
                        position: relative;
                        width: auto;
                        color: $white;
                        font-size: 1.6rem;
                        display: block;
                        padding-bottom: 5px;
                        border-bottom: 2px solid transparent;

                        &:focus {
                            outline: unset;
                        }

                        @include media($desktop) {
                            font-size: 1.5rem;
                        }

                        @include media($tablet) {
                            @include intersemibold;
                            font-size: 2rem;
                            padding: 30px;
                            width: 100%;
                            text-transform: uppercase;
                        }

                        &:hover {
                            color: $yellow;
                            @media all and (min-width: 992px) {
                                border-bottom: 2px solid $yellow;
                            }
                        }
                    }

                    &:first-child a {
                        padding-left: 0;

                        @include media($tablet) {
                            padding-left: 30px;
                        }
                    }

                    &.current-menu-parent {
                        > a {
                            color: $yellow;
                            border-bottom: 2px solid $yellow;
                        }
                    }

                    .button {
                        width: 22rem;
                        font-size: 1.3rem;

                        @include media($desktop) {
                            padding-right: 12px;
                            width: 21rem;
                            font-size: 1.3rem;
                        }

                        @include media($mobile) {
                            padding-right: 0;
                        }

                        &:hover {
                            padding-right: 40px;

                            @include media($mobile) {
                                padding-left: 10px;
                            }
                        }
                    }

                    &.current-menu-item {
                        a {
                            color: $yellow;

                            @media all and (min-width: 992px) {
                                border-bottom: 2px solid $yellow;
                            }
                        }
                    }
                }

                &.sub-menu {
                    display: none;
                    flex-direction: column;

                    li:first-child {
                        a {
                            padding-left: 45px;
                        }
                    }

                    li a {
                        @include media($tablet) {
                            padding-left: 45px;
                        }
                    }
                }

                &.header_menu li:first-child {
                    @include media($tablet) {
                        padding-top: 40px;
                    }

                    a {
                        @include media($tablet) {
                            margin-left: auto;
                            margin-right: auto;
                        }
                    }
                }

                &.sub-menu li:hover .sub-menu li a:hover {
                    color: $yellow;
                    border-color: $yellow;

                    &:before {
                        content: "";
                        background: url("../images/right-arrow.png") no-repeat;
                        position: absolute;
                        width: 6px;
                        height: 14px;
                        left: 46px;
                        top: 21px;
                        background-size: 100%;

                        @include media($tablet) {
                            top: 34px;
                            left: 25px;
                        }
                    }
                }
            }

            .right_nav {
                ul li:last-child {
                    @include media($tablet) {
                        margin-top: 40px;
                        padding-top: 40px;
                        border-top: 1px solid;
                        border-color: rgba(255, 255, 255, 0.2);
                    }
                    a {
                        @include media($tablet) {
                            padding-left: 0;
                            margin: 0 auto;
                        }

                        @include media($mobile) {
                            padding-left: 10px;
                        }
                    }
                }
            }
        }

        &.member {
            background: rgba(0, 0, 0, 0.9);

            .menu {
                nav {
                    justify-content: flex-end;
                }
            }
        }
    }

    &.slide {
        background: rgba(0, 0, 0, 0.9);

        .header_bottom {
            .menu {
                //top: 125px;
                left: 0;
            }
        }
    }
}

.sub_header {
    @include intermedium;
    color: $white;
    margin: 80px 0;

    @include media($tablet) {
        padding: 20px 0;
        font-size: 34px;
        margin: 40px 0 20px 0;
    }

    @include media($mobile) {
        margin: 0 0 20px;
        padding: 15px 0;
        font-size: 22px;
    }

    h2 {
        margin: 0;
        text-transform: capitalize;
    }
}

.mobile_menu_icon {
    position: relative;
    display: none;
    width: 30px;
    height: 24px;
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
    -webkit-transition: 0.5s ease-in-out;
    -moz-transition: 0.5s ease-in-out;
    -o-transition: 0.5s ease-in-out;
    transition: 0.5s ease-in-out;
    cursor: pointer;
    margin-left: auto;

    span {
        position: absolute;
        height: 5px;
        width: 100%;
        background-color: $white;
        border-radius: 9px;
        opacity: 1;
        left: 0;
        -webkit-transform: rotate(0deg);
        -moz-transform: rotate(0deg);
        -o-transform: rotate(0deg);
        transform: rotate(0deg);
        -webkit-transition: 0.25s ease-in-out;
        -moz-transition: 0.25s ease-in-out;
        -o-transition: 0.25s ease-in-out;
        transition: 0.25s ease-in-out;

        &:nth-child(1) {
            top: 1px;
        }

        &:nth-child(2) {
            top: 10px;
        }

        &:nth-child(3) {
            top: 19px;
        }
    }

    &.open span {
        &:nth-child(1) {
            top: 12px;
            -webkit-transform: rotate(135deg);
            -moz-transform: rotate(135deg);
            -o-transform: rotate(135deg);
            transform: rotate(135deg);
        }

        &:nth-child(2) {
            opacity: 0;
            left: -60px;
        }

        &:nth-child(3) {
            top: 12px;
            -webkit-transform: rotate(-135deg);
            -moz-transform: rotate(-135deg);
            -o-transform: rotate(-135deg);
            transform: rotate(-135deg);
        }
    }

    @include media($tablet) {
        display: block;
    }
}

ul.member_menu {
    position: relative;
    text-align: left;
    gap: 4rem;

    @include media($tablet) {
        gap: 0;
        min-height: 1500px;
    }

    > li {
        position: relative;

        @include media($tablet) {
            border: 1px solid;
            border-color: rgba(255, 255, 255, 0.1);
        }

        &:last-child {
            padding-right: 0;

            .sub-menu {
                left: auto;
                right: -20px;

                @include media($mobile) {
                    right: 0;
                }
            }
        }

        &.open {
            padding-bottom: 0 !important;
        }

        &.current-menu-parent {
            > a {
                color: $yellow;
            }

            ul li.current_page_item a:hover {
                color: $white;
            }
        }

        &.menu-item-has-children > a {
            position: relative;

            &:after {
                @include media($tablet) {
                    content: "+";
                    position: absolute;
                    right: 20px;
                    font-size: 20px;
                }
            }

            &.open {
                &:after {
                    content: "-";
                    font-size: 30px;
                }
            }
        }

        ul.sub-menu {
            background: transparent;
            position: absolute;
            left: -5px;
            padding-left: 0;
            white-space: nowrap;
            top: 100%;
            border-bottom-left-radius: 4px;
            border-bottom-right-radius: 4px;
            -webkit-border-bottom-right-radius: 4px;
            -webkit-border-bottom-left-radius: 4px;
            -moz-border-radius-bottomright: 4px;
            -moz-border-radius-bottomleft: 4px;
            padding-top: 30px;

            @include media($tablet) {
                position: relative;
                margin-top: 15px;
                padding-top: 0;
                top: 0;
            }

            li {
                float: none;
                display: block;
                padding: 0 !important;
                position: relative;
                width: 100% !important;
                background: $black;

                @include media($mobile) {
                    padding: 0 0 0 10px !important;
                }

                &:last-child {
                    @include media($mobile) {
                        border-bottom: none;
                    }
                }

                &:after {
                    content: "" !important;
                }

                &:hover {
                    a {
                        color: $yellow;

                        &:before {
                            content: "";
                            background: url("../images/icon-right-triangle.png")
                                no-repeat;
                            position: absolute;
                            background-size: 100%;
                            left: 18px;
                            top: 20px;
                            height: 18px;
                            opacity: 100%;
                            width: 8px;
                        }
                    }
                }

                &.menu-item-has-children:hover {
                    a {
                        color: $yellow;

                        &:before {
                            background: url("../images/right-arrow-gray.png")
                                no-repeat;
                            background-size: 100%;
                        }
                    }

                    > a {
                        color: $yellow;

                        &:before {
                            background: url("../images/right-arrow.png")
                                no-repeat;
                            background-size: 100%;
                        }
                    }
                }

                a {
                    position: relative;
                    display: block;
                    padding: 15px 45px 20px;
                    text-transform: none;

                    &:before {
                        content: "";
                        background: url("../images/icon-right-triangle.png")
                            no-repeat;
                        opacity: 0%;
                        -webkit-transition: 0.5s ease-in-out;
                        -moz-transition: 0.5s ease-in-out;
                        -o-transition: 0.5s ease-in-out;
                        transition: 0.5s ease-in-out;
                        width: 0;
                        position: absolute;
                        height: 25px;
                        background-size: 100%;
                        left: 18px;
                        top: 20px;

                        @include media($tablet) {
                            top: 34px;
                            left: 25px;
                        }

                        @include media($mobile) {
                            background-size: 100%;
                        }
                    }

                    &:not(.button) {
                        padding-bottom: 20px !important;
                        border-bottom: 1px solid !important;
                        border-color: rgba(255, 255, 255, 0.2) !important;

                        &:hover {
                            border-color: $yellow !important;
                            &:before {
                                content: "";
                                background: url("../images/icon-right-triangle.png")
                                    no-repeat;
                                position: absolute;
                                background-size: 100%;
                                left: 18px;
                                top: 20px;
                                height: 18px;
                                opacity: 100%;
                                width: 8px;

                                @include media($tablet) {
                                    top: 34px;
                                    left: 25px;
                                }
                            }
                        }
                    }
                }

                &.current_page_item {
                    a {
                        &:before {
                            content: "";
                            background: url("../images/icon-right-triangle.png")
                                no-repeat;
                            background-size: 100%;
                            position: absolute;
                            left: 18px;
                            top: 20px;
                            height: 18px;
                            opacity: 100%;
                            width: 8px;

                            @include media($tablet) {
                                top: 34px;
                                left: 25px;
                            }
                        }
                    }
                }

                &.current-menu-item {
                    a {
                        border-bottom: 1px solid !important;
                        border-color: $yellow !important;
                    }
                }

                .sub-menu {
                    width: 100%;
                    left: 0;
                    top: 54px;
                    white-space: initial;

                    @media screen and (min-width: 769px) {
                        max-height: 40vh;
                        overflow: hidden scroll;
                    }

                    @include media($mobile) {
                        top: 0;
                        margin-top: 0;
                    }

                    a {
                        font-size: 16px;
                        padding-left: 62px;

                        &:before {
                            left: 38px;
                        }

                        &:hover {
                            color: $black;

                            &:before {
                                background: url("../images/right-arrow.png")
                                    no-repeat;
                                background-size: 100%;
                            }
                        }
                    }
                }
            }
        }
    }
}

.fancybox-skin {
    padding: 0 !important;
}

.fancybox-inner {
    background: rgba(0, 0, 0, 0.7);
}

.fancybox-slide {
    padding: 0 20px !important;
    overflow: hidden !important;
}

#email_join,
#members_only_content {
    width: 40%;
    text-align: center;
    background: $darkerGray;
    color: $white;
    padding: 20px 5px 150px;
    max-width: 900px;
    min-width: 700px;
    border-radius: 4px;

    @include media($tablet) {
        min-width: 650px;
    }

    @include media($mobile) {
        padding: 8px 5px 20%;
        width: 100%;
        min-width: unset;
    }

    a {
        display: inline-block;

        &:focus {
            outline: unset;
        }
    }

    &.active {
        display: block;
    }

    .logo {
        max-width: 300px;
        text-align: center;

        @include media($mobile) {
            max-width: 250px;
        }

        img {
            margin: 0 auto;
        }
    }

    h2 {
        @include span-columns(12);
        text-transform: uppercase;
        line-height: 1.6;
        font-size: 3.8rem;

        span {
            color: $red;
        }

        @include media($mobile) {
            font-size: 4.8vw;
        }
    }

    p {
        @include span-columns(12);
        margin-bottom: 20px;
        @include exSmallFont;
    }
    #mc_embed_signup {
        @include span-columns(12);
        position: relative;
        width: 100% !important;

        form {
            position: relative;
            margin: 0 auto;
            width: 90%;

            @include media($mobile) {
                width: 100%;
            }

            .g-recaptcha > div {
                margin: 20px auto;
            }
        }

        #subscribe-result {
            margin-top: 2%;
            font-size: 18px;

            @include media($mobile) {
                font-size: 14px;
            }
        }

        input[type="email"] {
            width: 100%;
            background-color: $black;
            height: 55px;
            padding: 0 20px;
            display: inline-block;
            font-size: 18px;
            border: 1px solid $borderGray;
            border-radius: 4px;

            @include media($mobile) {
                font-size: 16px;
                padding: 10px;
                height: 41px;
            }
        }
        @include basicButton;
        .button {
            margin-top: 0;
            width: 100%;
        }
    }
}
